.page-footer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 320px;
  min-height: 235px;
  margin: 0 auto;
  padding-top: 33px;
  padding-bottom: 26px;

  @media screen and (min-width: 768px) {
    flex-direction: row;
    width: 768px;
    min-height: 120px;
    padding: 0 8%;
    padding-bottom: 10px;
  }

  @media screen and (min-width: 1200px) {
    width: 1200px;
    padding-left: 120px;
    padding-right: 120px;
  }
}

.page-footer__hashtag {
  padding: 0;
  margin: 0;
  font-weight: bold;
  font-size: 21px;
  line-height: 26px;
}

.page-footer__dev {
  position: relative;
  padding-right: 40px;
  color: var(--text-main);
  line-height: 26px;

  svg {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
  }

  &:hover,
  &:focus svg {
    fill: $main;
  }

  &:active svg {
    fill: initial;
    opacity: 0.3;
  }
}
