.social {
  list-style: none;
  display: flex;
}

.social__item {
  width: 46px;
  height: 48px;
  margin-right: 6px;
  background-color: $main;

  &:last-child {
    margin-right: 0;
  }

  &:hover,
  &:focus {
    background-color: $main-hover;
  }

  &:active {
    background-color: $main-active;
  }
}

.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;


  &:active {
    opacity: 0.6;
  }
}

.social__icon {
  fill: $text-accent;
}
